body {
  background: #f4f4f4;
  font-family: Helvetica, Arial, sans-serif; }

h1, h2, h3 {
  color: #2ecc71;
  font-weight: bold; }

h3 {
  font-size: 14px;
  line-height: 16px; }

h4 {
  color: #000;
  font-size: 10px;
  letter-spacing: -0.00833333px;
  mix-blend-mode: normal;
  opacity: 0.38; }

p {
  color: #000;
  font-size: 14px;
  line-height: 16px; }

.img-responsive {
  height: auto;
  max-width: 100%; }



